:root {
  --duration-timing: 250ms cubic-bezier(.4, 0, .2, 1);
  --indent: 20px;
  --items: 4;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #eee;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.search-form {
  width: 100%;
  background: linear-gradient(to right, #145901, #05f00d);
  justify-content: center;
  align-items: center;
  padding: 25px 0;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 3px 5px rgba(0, 0, 0, .5);
}

.input {
  width: 180px;
  height: 40px;
  border: none;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  outline: none;
  padding: 10px;
  font-size: 16px;
}

.search-button {
  width: 100px;
  height: 40px;
  cursor: pointer;
  border: none;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 16px;
  transition: background-color .3s linear, color .3s linear;
}

.search-form__button :hover, .search-form__button :focus {
  color: #fff;
  background-color: #0cc854;
}

.gallery {
  grid-gap: 10px;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  padding: 30px;
  display: grid;
}

.photo-card {
  background-color: #fff;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.thumb {
  height: 200px;
  width: 100%;
}

.img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  border-radius: 5px 5px 0 0;
  display: block;
}

.info {
  justify-content: space-between;
  align-items: flex-end;
  gap: 15px;
  padding: 10px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  font-size: small;
  display: flex;
}

.photo-card {
  transition: box-shadow var(--duration-timing);
  transition: box-shadow var(--duration-timing);
  display: block;
  box-shadow: 0 1px 2px rgba(60, 64, 67, .3), 0 2px 6px 2px rgba(60, 64, 67, .15);
}

.photo-card:hover, .photo-card:focus {
  cursor: pointer;
  box-shadow: 3px 3px 3px rgba(81, 81, 199, .12), 0 4px 4px #5ab21b, 1px 4px 6px rgba(81, 81, 199, .16);
}

.footer {
  justify-content: center;
  align-items: center;
  padding-bottom: 30px;
  display: flex;
}

.load-more {
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #63a736;
  border-style: none;
  border-radius: 5px;
  padding: 15px 20px;
  font-size: medium;
  font-weight: 500;
}

.end-collection {
  text-align: center;
  margin: 30px 0;
  font-size: 20px;
}

.is-hidden {
  display: none;
}

